body {
  background-color: #0e1821;
  color: #dbd7d4;
  overflow: hidden;
  display: block;
}
.logo {
  background: #070707;
  display: block;
  padding: 8px 0;
  font-family: 'Permanent Marker', cursive;
  /* font-family: 'Shadows Into Light', cursive; */
  color: gold;
  text-shadow: 2px 2px 4px #0da15c;
}
.logo .t {
  display: block;
  margin: 8px auto;
  width: 24px;
  height: auto;
}

.logo .theo {
  display: block;
  margin: 8px auto;
  width: 24px;
  height: auto;
  font-family: 'Covered By Your Grace', cursive;
  color: antiquewhite;
}
a {
  background-color: transparent;
  text-decoration: none;
}
b {
  font-family: 'Covered By Your Grace', cursive;
  font-size: medium;
}
#nav-bar {
  background-color: #131317;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  z-index: 3;
  height: 100vh;
  width: 60px;
}
#nav-bar nav {
  display: block;
  text-align: center;
}
#nav-bar .nav-toggle {
  display: none;
}
#nav-bar ul {
  display: block;
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 0;
  padding-bottom: 20px;
}
#nav-bar nav a {
  font-size: 20px;
  color: #909096;
  margin-bottom: 15px;
  display: block;
  line-height: 51px;
  height: 51px;
  position: relative;
  text-decoration: none;
}
#nav-bar ul li a {
  padding: 7px 0;
  display: block;
  font-size: 15px;
  line-height: 16px;
  color: #909096;
}
#nav-bar a:hover,
#nav-bar a:active {
  color: gold;
}
#nav-bar nav a b {
  display: none;
}
#nav-bar nav a:hover b {
  display: block;
  font-size: 11px;
  letter-spacing: 2px;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
#nav-bar nav a:hover i {
  opacity: 0;
  font-size: 9px;
  letter-spacing: 2px;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}


/* On screens that are 992px wide or less, the background color is blue */
@media screen and (max-width: 992px) {
  .logo {
    float: left;
    padding: 2px 0;
    margin-right: 20px;
    background: #070707;
    display: block;
  }
  .logo .t {
    height: 30px;
    display: block;
    margin: 5px 15px;
    width: auto;
  }
  .logo .theo {
    display: block;
    margin: 5px 15px;
    width: auto;
    height: auto;
  }
  body {
    overflow: auto;
  }
  #nav-bar nav a:first-child {
    margin-left: 0;
  }
  #nav-bar nav a {
    float: left;
    margin-left: 14%;
    line-height: 60px;
    height: 60px;
  }
  #nav-bar {
    display: flex;
    flex-direction: row;
    height: 60px;
    width: 100%;
    position: absolute;
  }
  #nav-bar .nav-toggle {
    display: none;
  }
  #nav-bar ul {
    position: static;
    right: 20px;
    width: 15%;
    height: 60px;
    line-height: 60px;
    margin-top: 10px;
    float: right;
    display: flex;
    justify-content: space-around;
  }
  #nav-bar nav {
    width: 42%;
    position: absolute;
    display: block;
    left: 50%;
    margin-left: -19%;
    float: left;
    overflow: hidden;
    text-align: center;
    height: 60px;
    top: 0;
    margin-top: 0;
  }
  b {
    display: none;
  }
}

/* On screens that are 600px wide or less, the background color is olive */
@media screen and (max-width: 600px) {
  #nav-bar {
    display: flex;
    flex-direction: row;
    height: 60px;
    width: 100%;
  }
  #nav-bar .nav-toggle {
    display: block;
    font-size: 30px;
    color: #fff;
    float: right;
    margin: 10px 20px 10px 10px;
    display: block;
  }
  #nav-bar ul {
    display: none;
  }
  #nav-bar nav.show {
    background-color: #131317;
    width: 100%;
    position: fixed;
    left: 100%;
    margin-left: 0;
    opacity: 0;
    float: left;
    overflow: hidden;
    text-align: center;
    height: 60px;
    top: 60px;
    margin-top: 0;
    -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  }
  #nav-bar nav a {
    float: left;
    margin-left: 0 !important;
    line-height: 60px;
    height: 60px;
    width: 20%;
    font-size: 20px;
    color: #909096;
    margin-bottom: 15px;
    display: block;
    line-height: 51px;
    height: 51px;
    position: relative;
    text-decoration: none;
    -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  }
  #nav-bar nav.show {
    left: 0;
    opacity: 0;
  }
  b {
    display: none;
  }
}
